@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
*{box-sizing:border-box;margin:0;padding:0;border:none;outline:none}body{background:pink;width:100%;height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}.container{border-radius:10px;width:300px;height:600px;padding:10px;box-shadow:0 5px 15px #000;border:1px solid #fff;background-position:center;background-size:cover}.bg-normal{background-image:url(/weather-app/static/media/normaljpeg.cc03c79a.jpeg)}.bg-snow{background-image:url(/weather-app/static/media/snow.7001a4ba.jpg)}.bg-hot{background-image:url(/weather-app/static/media/hot.d7ce0dd8.jpg)}.bg-rain{background-image:url(/weather-app/static/media/rain.98c3c5d6.jpg)}.bg-cloud{background-image:url(/weather-app/static/media/cloud.5fb30d82.jpg)}.bg-storm{background-image:url(/weather-app/static/media/storm.dcb7718a.jpg)}.bg-clear{background-image:url(/weather-app/static/media/clear.ed8101d4.jpg)}.cute-cloud{margin-top:50px;display:block;width:100%;height:auto}.anoucement{-webkit-transform:translate(0, -30px);transform:translate(0, -30px);text-align:center;color:white;font-size:36px;letter-spacing:2px;font-weight:200;font-family:'Fredoka One', cursive}.form__container{margin-top:10px}.form__group{position:relative}.form__group input{border-radius:50px;display:block;padding:8px 12px;padding-right:35px;width:100%;font-size:16px}.form__group button{cursor:pointer;background:none;position:absolute;top:5px;right:10px;text-align:center;width:28px;height:28px;font-size:20px;color:black}.city-weather__container{padding:20px 10px}.city-weather__header h1{color:white}.city-weather__header h4{color:white}.city-weather__main{margin-top:10px;display:grid;padding:5px 10px;border-radius:20px;border:2px solid white;grid-template-columns:auto auto}.city-weather__main .main-weather__icon{margin-top:5px}.city-weather__main .main-weather__icon img{width:50px;height:auto}.city-weather__main .main-weather__icon h4{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;color:white;text-transform:capitalize;font-size:16px}.city-weather__main .main-weather__tempature{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;color:white;font-size:60px;font-weight:bold}.city-weather__main .main-weather__more{padding:10px 0;color:white;font-size:16px;text-align:center;grid-column:1 / span 2;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.city-weather__weekday{margin-top:10px;display:grid;grid-template-columns:repeat(3, 1fr)}.city-weather__weekday__dow{border:2px solid white;border-left:none;border-top:none;padding:5px;text-align:center}.city-weather__weekday__dow:nth-child(4),.city-weather__weekday__dow:nth-child(1){border-left:2px solid white}.city-weather__weekday__dow:nth-child(1),.city-weather__weekday__dow:nth-child(2),.city-weather__weekday__dow:nth-child(3){border-top:2px solid white}

